import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { AccountsService } from '../../../.././api/services/accounts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-income-invoice-reverse',
  templateUrl: './income-invoice-reverse.component.html',
  styleUrls: ['./income-invoice-reverse.component.scss']
})
export class IncomeInvoiceReverseComponent implements OnInit {
  
  isInvoiceSubmitted:boolean = true;

  isError:boolean = false;
  alertMessage:string = "";

  @Input() invoice:any;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
    private accountsService:AccountsService,
  	private sharedService:SharedService,
  	private lookupService:LookupService,
  	private cookieService:CookieService
  ) { }

  submitReverseIncomeInvoiceForm(form){

  	this.isInvoiceSubmitted = false;

  	let details = {
  		
		"apartmentId": parseInt(this.cookieService.get('apartmentId')),
		"amount": this.invoice.custInvoiceAmount,
		"collectionId": 0,
		"invoiceId": this.invoice.custInvoiceId,
		"isEmailSent": true,
		"isSmssent": true,
		"comment": this.invoice.comment,
		"isActive": true,
		"insertedBy": parseInt(this.cookieService.get('userId')),
		"insertedOn": new Date().toISOString(),
		"updatedBy": null,
		"updatedOn": null,
		"transReference1": "string",
		"transReference2": "string",
		"glAcctIndicator": "Income"

  	}

  	this.accountsService.addCustTransReversal(details).subscribe((res:any) => {

      if(res.message){

  			this.isInvoiceSubmitted = true;
  			this.sharedService.setAlertMessage("Invoice reversed successfully");

      }
      else {

  			this.isInvoiceSubmitted = true;
  			this.isError = true;
  			this.alertMessage = res.errorMessage;

      }

      },
    error => {
      this.isInvoiceSubmitted = true;
      this.isError = true;
      this.alertMessage = "Some error occured";
    });



  }

  ngOnInit() {


  }

}
